<template>
  <div class="reviseConsignee">
    <van-nav-bar
      title="修改收货人"
      @click-left="$toPage(-1)"
      @click-right="onClickCommit"
    >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
      <span slot="right" style="font-size: 15px">保存</span>
    </van-nav-bar>
    <van-cell-group>
      <van-field
        v-model="username"
        label="收货人"
        maxlength="10"
        placeholder="请输入用户名"
        size="lager"
      />
      <van-field
        v-model="userphone"
        label="手机号"
        type="tel"
        maxlength="11"
        :error-message="message"
        placeholder="请输入手机号"
        @focus="message = ''"
      />
    </van-cell-group>
    <van-cell-group>
      <van-cell
        clickable
        title="设为默认"
        @click="toggle"
      >
        <van-checkbox v-model="radio" />
      </van-cell>
    </van-cell-group>
  </div>
</template>
<script>
export default {
  data() {
    return {
      username: '',
      userphone: '',
      radio: 0,
      index: 0,
      shipid: 0,
      message: ''
    }
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    }
  },
  mounted() {
    this.getConsignee()
  },
  methods: {
    onClickBack() {
      this.$router.go('-1')
    },
    onClickCommit() {
      if(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.userphone)){
        JNB.query({
          serverName: 'userServer',
          cls: 'MyDrugStoreInfoModule',
          method: 'optConsigneeForApp',
          params: {
            shipid: this.shipid,
            compid: this.userInfo.comp.storeId,
            contactname: this.username,
            contactphone: this.userphone,
            cstatus: this.radio ? 2 : 0
          },
          callback: (data)=>{
            let result = JSON.parse(data)
            if (result.code === 200) {
              // 更新用户登录状态
              this.$toast.success('保存成功')
              this.$router.replace({
                path: '/manageConsignee'
              })
            }else {
              this.$toast.fail(result.message)
            }
          }
        })
      }else{
        this.message = '手机号格式错误！'
      }
    },
    toggle() {
      this.radio = !this.radio
    },
    getConsignee() {
      this.index = this.$route.query.index
      JNB.query({
        serverName: 'userServer',
        cls: 'MyDrugStoreInfoModule',
        method: 'queryMyConsignee',
        params: {
          compid: this.userInfo.comp.storeId
        },
        callback: (data)=>{
          let result = JSON.parse(data)
          if (result.code === 200) {
            // 更新用户登录状态
            this.username = result.data[this.index].contactname
            this.userphone = result.data[this.index].contactphone
            this.radio = result.data[this.index].cstatus === 2 ? true : false
            this.shipid = result.data[this.index].shipid

          }else {
            this.$toast.fail(result.message)
          }
        }
      })
    }
  }
}
</script>

